:root {
  --primary-color: #242A32;
  --point-color: #0296E5;
  --black: #3A3F47;
  --grey: #67686D;
  --font-color: #fff;
}

/* COMMON */
* {
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: var(--font-color);
}

ul,
ol {
  list-style: none;
}

h1 {
  padding: 0 !important;
  margin: 0 !important;
}

p {
  color: var(--font-color);
}

.logo {
  display: flex;
  align-items: center;
  color: var(--point-color);
  font-size: 1.7rem;
  margin-top: 2.8rem;
  margin-bottom: 2rem;
}

.logo-icon {
  width: 7rem;
}

body {
  /* max-width: 960px; */
  height: calc(100vh - 80px);
  padding: 0 2rem;
  background: var(--primary-color);
  color: #333;
}

.loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {}

.input_div {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.input_search {
  position: relative;
  width: 100%;
  max-width: 100%;
  height: 50px;
  padding: 5px 10px;
  margin: 26px 35px 8rem;
  border-radius: 7px;
  border-top: 1px solid transparent;
  border-left: 1px solid transparent;
  border-right: 1px solid transparent;
  background-color: #000;
  color: #fff;
  font-size: 1.2rem;
}

.movie {
  background: skyblue;
}

